* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 20px;
}

input {
  width: 100%;
  font-size: 1.3rem;
  color: gray;
  border: none;
  outline: none;
}

input:focus {
  border-bottom: 1px solid green;
}

.card {
  background-color: rgb(0, 140, 255);
  width: 250px;
  height: 250px;
  padding: 20px;
  margin: 20px;
}

.card .card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
